.container {
    width: 100%;
    padding: 0 1em;
    margin-left: auto;
    margin-right: auto;
}

@media (min-width:576px) {
    .container {
        max-width: 540px
    }
}

@media (min-width:768px) {
    .container {
        max-width: 720px
    }
}

@media (min-width:992px) {
    .container {
        max-width: 960px
    }
}

@media (min-width:1200px) {
    .container {
        max-width: 1140px
    }
}

@media (min-width:1440px) {
    .container {
        max-width: 1280px
    }
}